import * as React from 'react';

export interface Props {
  src: string;
  alt?: string;
  sx?: any;
}

const imageStyles = {
  width: '100%',
  maxWidth: '100%',
  display: 'block',
};

function Image(props: Props) {
  const { src } = props;
  if (!src) {
    throw new Error("You can't render an empty image!");
  }

  if (src.startsWith('/') || src.startsWith('http://') || src.startsWith('https://')) {
    return <img src={src} alt={props.alt || ''} style={{ ...imageStyles, ...props.sx }}></img>;
  } else {
    throw new Error('Not supported yet.');
  }
}

export default Image;
